// Typography
//
// Headings, body text, lists, and other misc typographic elements.

h1, h2, h3, h4, h5, h6 {
  padding-top: 1rem;
  margin-bottom: .5rem;
  font-weight: bold;
  line-height: 1.25;
  color: var(--text-main-color);
  text-rendering: optimizeLegibility;
  scroll-margin-top: 4rem;

  a {
    font-size: 80%;
    font-weight: normal;
  }
}

h1 {
  font-size: 2rem;
}

h2 {
  margin-top: 1rem;
  font-size: 1.5rem;
}

h3 {
  margin-top: 1.5rem;
  font-size: 1.25rem;
}

h4, h5, h6 {
  margin-top: 1rem;
  font-size: 1rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
  color: var(--text-alt1-color);
}

strong {
  color: var(--text-strong-color);
}

em {
  color: var(--text-main-color);
}

ul, ol, dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

dt {
  font-weight: bold;
}

dd {
  margin-bottom: .5rem;
}

hr {
  position: relative;
  margin: 1.5rem 0;
  border: 0;
  border-top: 1px solid var(--text-alt2-color);
}

abbr[data-title] {
  position: relative;
  font-size: 90%;
  font-weight: bold;
  color: var(--text-strong-color);
  text-decoration: underline dotted;

  &:hover::after,
  &:focus::after {
    content: attr(data-title);

    position: absolute;
    left: 0;
    bottom: -30px;
    width: auto;
    white-space: nowrap;

    background-color: var(--background-alt2-color);
    color: var(--text-alt1-color);
    border-radius: 3px;
    box-shadow: 1px 1px 5px 0 rgba(0,0,0,0.4);
    font-size: 14px;
    padding: 3px 5px;
  }
}

blockquote {
  padding: .5rem 1rem;
  margin: .8rem 0;
  color: var(--text-alt1-color);
  border-left: .25rem solid var(--text-alt2-color);

  p:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 30em) {
    padding-right: 5rem;
    padding-left: 1.25rem;
  }
}


// Markdown footnotes
//
// See the example content post for an example.

[id^="#fn:"], [id^="fnref:"] {
  scroll-margin-top: 4rem;
}

// Footnote number within body text
a[href^="#fn:"],
// Back to footnote link
a[href^="#fnref:"] {
  display: inline-block;
  margin-left: .1rem;
  font-weight: bold;
}

// List of footnotes
.footnotes {
  margin-top: 2rem;
  font-size: 85%;
}

// Custom type
//
// Extend paragraphs with `.lead` for larger introductory text.

.lead {
  font-size: 1.25rem;
  font-weight: 300;
}

article * img {
  width: 100%;
  height: auto;
}